@import url("https://fonts.googleapis.com/css2?family=Caveat&family=Dancing+Script&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Sora&display=swap");

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Sora", sans-serif;
  font-display: swap;
  color: #06091d;
}

body,
html,
#root {
  height: 100%;
}

// app

.app {
  margin: 0 auto;
  max-width: 900px;

  &__navbar {
    color: #06091d;
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 30px 0;
    width: 100%;
  }

  &__topic {
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__footer {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 1rem;
    padding-top: 1rem;
    width: 100%;
  }
}

// navbar

.navbar {
  &__logo {
    font-size: 36px;
    font-family: "Caveat";
    font-weight: bold;
  }

  &__links {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 300px;
  }

  &__link {
    font-size: 16px;
    color: #06091d;
    text-decoration: none;
    padding: 0px 5px;
  }

  &__link:hover {
    cursor: pointer;
    color: darkcyan;
    font-weight: bold;
  }
}

// topic

.topic {
  &__about-me {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-content: center;
    background-color: rgb(238, 238, 238);
    border-radius: 40px;
  }

  &__skills,
  &__projects,
  &__contact {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
}

.about-me {
  &__avatar {
    border-radius: 50%;
    padding-bottom: 100px;
  }

  &__description {
    margin: 0 50px;
  }
}

.description {
  &__headline {
    text-align: left;
    font-size: 36px;
  }

  &__text {
    text-align: left;
    font-size: 20px;
    margin-top: 20px;
    line-height: 30px;
  }
}

// skills

.skills {
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    justify-items: center;
  }

  &__list-item {
    padding: 10px;
    border-radius: 10px;
    text-align: center;
  }

  &__text {
    font-size: 16px;
    margin-bottom: 20px;
  }
}

h5 {
  font-size: 24px;
  margin: 0;
  // font-style: italic;
  color: darkcyan;
  font-family: "Caveat";
}

.list-item {
  &__image {
    margin-bottom: 12px;
    width: 60px;
    height: 60px;
  }
}

.topic-headline {
  font-size: 32px;
  margin-bottom: 30px;
  text-decoration: underline;
  font-weight: bold;
  text-underline-offset: 10px;
}

// projects

.projects {
  &__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}

.project {
  display: flex;
  flex-direction: column;
  width: 100%;

  &__title {
    font-size: 18px;
    margin-bottom: 10px;
    font-weight: bold;
  }

  &__prev {
    object-fit: cover;
    width: 100%;
    height: 18rem;
    object-position: 50% 0;
    border-radius: 20px;
    border: 2px solid #f0f0f0;
  }

  &__menu {
    display: flex;
    margin-top: auto;
    width: 100%;
  }

  &__menu-link {
    text-decoration: none;
    color: #06091d;
    font-size: 14px;
    padding: 5px 10px;
    border-radius: 10px;
    background-color: rgb(238, 238, 238);
  }

  &__menu-link:not(:last-child) {
    margin-right: 10px;
  }

  &__menu-link:hover {
    background-color: darkcyan;
    border-radius: 10px;
    color: white;
  }
}

// contact

.contact {
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
    width: 100%;
  }

  &__social-link {
    text-decoration: none;
    color: #3e4c59;
    font-size: 24px;
    padding: 0 10px;
    display: flex;
    align-items: center;
    margin-top: 20px;
  }
}

.social-link {
  &__link {
    text-decoration: none;
    color: #3e4c59;
    font-size: 16px;
    padding: 0 10px;
  }

  &__logo {
    width: 25px;
  }

  &__link:hover {
    cursor: pointer;
    color: darkcyan;
    font-weight: bold;
  }
}

// footer

.footer {
  &__text {
    font-size: 14px;
    color: #3e4c59;
  }

  &__emoji {
    width: 1em;
  }
}
